import { usersAPI } from "../api/api";

const FOLLOW = 'users/FOLLOW';
const UNFOLLOW = 'users/UNFOLLOW';
const SET_USERS = 'users/SET_USERS';
const GET_TOTAL_COUNT = 'users/GET_TOTAL_COUNT';
const SET_CURRENT_PAGE = 'users/SET_CURRENT_PAGE';
const TOGGLE_IS_FETCHING = 'users/TOGGLE_IS_FETCHING';
const TOGGLE_FOLLOWING_IN_PROGRESS = 'users/TOGGLE_FOLLOWING_IN_PROGRESS';



let initialState = {
    users: [],
    totalCount: 1,
    pageSize: 10,
    currentPage: 1,
    isFetching: true,
    followingInProgress: [],
    fake: 1
}

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: state.users.map(elem => {
                    if (elem.id === action.userId) {
                        return {
                            ...elem, followed: true
                        }
                    }
                    return { ...elem }
                })
            }
        case UNFOLLOW:
            return {
                ...state,
                users: state.users.map(elem => {
                    if (elem.id === action.userId) {


                        return {
                            ...elem, followed: false
                        }
                    }
                    return { ...elem }
                })
            }
        case SET_USERS:
            return {
                ...state,
                users: [...action.users]
            }
        case GET_TOTAL_COUNT:
            return {
                ...state, totalCount: action.totalCount
            }
        case SET_CURRENT_PAGE:
            return {
                ...state, currentPage: action.currentPage
            }
        case TOGGLE_IS_FETCHING:
            return {
                ...state, isFetching: action.isFetching
            }
        case TOGGLE_FOLLOWING_IN_PROGRESS:
            return {
                ...state,
                followingInProgress: [action.isFetching
                    ? action.userId
                    : state.followingInProgress.filter(userId => userId === !action.userId)]
            }
        default: return state;
    }
}

export const followSuccess = (userId) => ({ type: FOLLOW, userId: userId });
export const unFollowSuccess = (userId) => ({ type: UNFOLLOW, userId: userId });
export const setUsers = (users) => ({ type: SET_USERS, users: users });
export const getTotalCount = (totalCount) => ({ type: GET_TOTAL_COUNT, totalCount });
export const setCurrentPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage });
export const toggleIsFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching });
export const toggleFollowingInProgress = (isFetching, userId) => ({ type: TOGGLE_FOLLOWING_IN_PROGRESS, isFetching, userId });


export const requestUsers = (currentPage, pageSize) => {
    return async (dispatch) => {
        dispatch(toggleIsFetching(true));

        let data = await usersAPI.getUsers(currentPage, pageSize)
        dispatch(setCurrentPage(currentPage))
        dispatch(toggleIsFetching(false));
        dispatch(setUsers(data.items));
        dispatch(getTotalCount(data.totalCount));
    }
}

export const follow = (userId) => {
    return async (dispatch) => {
        dispatch(toggleFollowingInProgress(true, userId))
        let data = await usersAPI.follow(userId)
        if (data.resultCode === 0) {
            dispatch(followSuccess(userId))
            dispatch(toggleFollowingInProgress(false, userId))
        }
    }
}

export const unFollow = (userId) => {
    return async (dispatch) => {
        dispatch(toggleFollowingInProgress(true, userId))
        let data = await usersAPI.unfollow(userId)
        if (data.resultCode === 0) {
            dispatch(unFollowSuccess(userId))
            dispatch(toggleFollowingInProgress(false, userId))

        }
    }
}

export default usersReducer;