import React from 'react';
import Preloader from '../../common/Preloader/Preloader';
import style from './ProfileInfo.module.css';
import ProfileStatus from './ProfileStatus';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';

const ProfileInfo = (props) => {
    // debugger;
    if (!props.currentUserProfile) {
        return <Preloader />
    }
    return (
        <div className={style.profile__info}>
            <img src="https://img1.goodfon.ru/original/5760x3840/6/7e/tropiki-more-poberezhe-plyazh-2125.jpg" className={style.image} />

            <ProfileStatusWithHooks updateProfileStatus={props.updateProfileStatus} status={props.status} />

            <div><img src={props.currentUserProfile.photos.large} /></div>
        </div>
    )
}


export default ProfileInfo;