import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { maxLengthCreator, minLengthCreator, required } from '../../utils/validators/validators';
import { Element } from '../common/FormControl/FormControl';
import style from './Login.module.css'
import { connect } from 'react-redux'
import { loginActionCreator } from '../../redux/authReducer';
import { Navigate } from 'react-router-dom';
import styleForm from '../common/FormControl/FormControl.module.css'



const LoginForm = (props) => {

    const maxLength20 = maxLengthCreator(20);
    const minLength5 = minLengthCreator(5);

    const Input = Element('input');
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field placeholder={'Email'} name={'email'} component={Input} validate={[required, maxLength20, minLength5]} />
            </div>
            <div>
                <Field placeholder={'Password'} type={'password'} name={'password'} component={Input} validate={[required, maxLength20, minLength5]} />
            </div>
            <div>
                <Field type={'checkbox'} name={'rememberMe'} component={'input'} />
                Remember me
            </div>
            <div className={styleForm.commonErrorForm}>
                {props.error}
            </div>
            <div>
                <button type={'submit'}>Login</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({ form: 'login' })(LoginForm)


const Login = (props) => {

    let onSubmit = (formData) => {
        props.loginActionCreator(formData.email, formData.password, formData.rememberMe)
    }

    if (props.isAuth) {
        return <Navigate to={'/profile'} />
    }
    return <div>
        <div className={style.titlePage}>LOGIN</div>
        <LoginReduxForm onSubmit={onSubmit} />
    </div>

}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})
const loginContainer = connect(mapStateToProps, { loginActionCreator })(Login)
export default loginContainer;