import React from 'react';
import style from './Profile.module.css';
import Profile from './Profile';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { getProfile, updateProfileStatus, getProfileStatus } from "../../redux/profileReducer";
import { compose } from 'redux';
import { Navigate } from 'react-router-dom';


export function withRouter(Children) {
    return (props) => {

        const match = { params: useParams() };
        return <Children {...props} match={match} />
    }
}

class ProfileApiContainer extends React.Component {
    state = {
        userId: true
    }
    componentDidMount = () => {

        let userId = this.props.match.params.userId;
        // debugger;
        if (userId) {
            this.props.getProfile(userId)
            this.props.getProfileStatus(userId)
        }
        if (!userId) {
            userId = this.props.autorizedUserId;

            if (!userId) {
                this.setState({
                    userId: false
                })
            } else {
                // debugger;
                this.props.getProfile(userId)
                this.props.getProfileStatus(userId)
            }
        }

    }
    componentDidUpdate = (prevProps, prevState) => {
        let arr = [prevProps, prevState]
        // debugger;
        if (!this.props.isAuth && !this.state.userId) {
            return <Navigate to={'/login'} />
        }
    }

    render() {
        if (!this.props.isAuth && !this.state.userId) {
            return <Navigate to={'/login'} />
        }
        return (
            <div className={style.profile}>
                <Profile {...this.props} status={this.props.status} updateProfileStatus={this.props.updateProfileStatus}
                    currentUserProfile={this.props.currentUserProfile} />
            </div>
        )
    }

}

const mapStateToProps = (state) => {
    // debugger;
    return {
        currentUserProfile: state.profilePage.currentUserProfile,
        status: state.profilePage.status,
        autorizedUserId: state.auth.userId,
        isAuth: state.auth.isAuth
    }
}

// compose(
//     connect(mapStateToProps, { getProfile }),
//     withRouter,
//     withAuthRedirect
// )(ProfileApiContainer)
// const AuthRedirectComponent = withAuthRedirect(ProfileApiContainer);

// let withUrlApiContainerConponenta = withRouter(AuthRedirectComponent);
// const ProfileContainer = connect(mapStateToProps, { getProfile })(withUrlApiContainerConponenta);
// const ProfileContainer = connect(mapStateToProps, { getCurrentUsersProfile })(ProfileApiContainer);


// export default ProfileContainer;
export default compose(
    connect(mapStateToProps, { getProfile, updateProfileStatus, getProfileStatus }),
    withRouter
)(ProfileApiContainer)