import { profileAPI, usersAPI } from "../api/api";

const ADD_POST = 'profile/ADD_POST';
const GET_CURRENT_USER_PROFILE = 'profile/GET_CURRENT_USER_PROFILE';
const SET_USER_STATUS = 'profile/SET_USER_STATUS';


const initialState = {
    posts: [
        { id: 1, likesCounter: 0, message: 'I am web developer' },
        { id: 2, likesCounter: 0, message: 'I learne React' },
        { id: 3, likesCounter: 0, message: 'yo yo yo' },
    ],
    currentUserProfile: null,
    status: ''

}
const profileReducer = (state = initialState, action) => {
    // debugger;
    switch (action.type) {

        case ADD_POST:
            let newPost = {
                id: 4,
                likesCounter: 0,
                message: action.addPostMessage
            }
            return {
                ...state,
                posts: [...state.posts, newPost]
            }
        case GET_CURRENT_USER_PROFILE:
            return {
                ...state, currentUserProfile: action.profile
            }
        case SET_USER_STATUS:
            return {
                ...state,
                status: action.status
            }
        default: return state;
    }
}



export const addPostAC = (addPostMessage) => ({ type: ADD_POST, addPostMessage });
export const getCurrentUsersProfile = (profile) => ({ type: GET_CURRENT_USER_PROFILE, profile });
export const setUserStatus = (status) => ({ type: SET_USER_STATUS, status });

export const getProfile = (userId) => {
    return async (dispatch) => {
        // debugger;

        let response = await usersAPI.getProfile(userId)
        dispatch(getCurrentUsersProfile(response))
    }
}

export const getProfileStatus = (userId) => {
    return async (dispatch) => {
        let response = await profileAPI.getProfileStatus(userId)
        dispatch(setUserStatus(response))
    }
}

export const updateProfileStatus = (status) => {
    return async (dispatch) => {

        let response = await profileAPI.updateProfileStatus(status)
        if (response.resultCode === 0) {
            dispatch(setUserStatus(status))
        }
    }
}


export default profileReducer;