import React from 'react';
import style from './Post.module.css';

const Post = (props) => {
    return (
        <div className={style.post}>
            <div className={style.top}>
                <img src="https://loginportal.funnyjunk.com/comments/+_e7e00c8f8a605ad40671872cc682ce62.jpg" className={style.avatar} />
                <div className={style.userName}></div>
            </div>
            <div className={style.post__text}>{props.message}</div>
            <button className={style.like}>like {props.likesCounter}</button>
        </div>
    )
}


export default Post;