const SEND_MESSAGE = 'dialogs/SEND_MESSAGE';


const initialState = {
    dialogsItems: [
        { id: 1, userName: 'Anton' },
        { id: 2, userName: 'Anastasia' },
        { id: 3, userName: 'Natasha' },
        { id: 4, userName: 'Mikhail' },
    ],
    dialogsMessages: [
        { id: 1, message: 'Hi people' },
        { id: 2, message: 'YoYoYo' },
        { id: 3, message: 'Are you going drink' },
        { id: 4, message: 'Hi people' }
    ]
}
const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {

        case SEND_MESSAGE:
            let newMessage = {
                id: 5,
                message: action.sendMessage.message
            }

            return {
                ...state,
                dialogsMessages: [...state.dialogsMessages, newMessage],
            }
        default: return state
    }
}


export const sendMessageAC = (sendMessage) => ({ type: SEND_MESSAGE, sendMessage });

export default dialogsReducer;