import React from 'react';
import Post from './Post/Post';
import style from './MyPosts.module.css';
import { Field, reduxForm } from 'redux-form';
import { required, maxLengthCreator } from '../../../utils/validators/validators.jsx'
import { Element } from '../../common/FormControl/FormControl';
// import { Textarea } from '../../common/FormControl/FormControl.jsx'

const maxLengthField10 = maxLengthCreator(10);
const Textarea = Element('textarea');

const AddMessagePostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field validate={[required, maxLengthField10]} placeholder={"Your post"} name={"addNewPostMessage"} component={Textarea} />
            </div>
            <div>
                <button>Add post</button>
            </div>
        </form>
    )
}

const AddMessagePostFormRedux = reduxForm({ form: "addPostMessage" })(AddMessagePostForm)

const MyPosts = (props) => {
    // debugger;

    let elementPosts = props.posts.map(elem => {
        return <Post message={elem.message} likesCounter={elem.likesCounter} />
    })

    const onSubmit = (formData) => {
        props.addPost(formData.addNewPostMessage)
    }
    return (
        <div className={style.posts}>
            <AddMessagePostFormRedux onSubmit={onSubmit} />
            {elementPosts}
        </div>
    )
}


export default MyPosts;